@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400&family=Kalam:wght@300;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;background-color:#000}section{display:flex;flex-direction:column;align-items:center;min-height:100vh}.section-content{width:50vw}.header-text{font-family:"Amatic SC",cursive;font-size:4vh;margin-top:7vh}.primary-color{color:#e7cb8f}.white{color:#fff}li{list-style:none;display:flex;align-items:flex-start;text-align:left;margin-top:10px}ul li::before{content:"";min-height:12px;min-width:12px;background-image:url(/react-portfolio/static/media/light-bulb-icon.7bbd7d8a.png);background-size:contain;background-repeat:no-repeat;margin-right:10px;margin-top:5px}.footer{display:flex;justify-content:center;align-items:center;width:100%;height:10vh}.white{color:#fff}.footer-icon{margin-right:15px}
.name{font-size:7vh;letter-spacing:10px;color:#000;z-index:9;display:inline-block;text-align:center;text-shadow:0px 0px 29px #e7cb8f}.blink{animation:letter-flicker 2s linear infinite;padding:0}.name-divider{margin-left:20px}@keyframes letter-flicker{0%{opacity:.1;text-shadow:0px 0px 29px #e7cb8f}2%{opacity:.1;text-shadow:0px 0px 29px #e7cb8f}4%{opacity:1;text-shadow:0px 0px 29px #e7cb8f}19%{opacity:1;text-shadow:0px 0px 29px #e7cb8f}21%{opacity:.1;text-shadow:0px 0px 29px #e7cb8f}23%{opacity:1;text-shadow:0px 0px 29px #e7cb8f}80%{opacity:1;text-shadow:0px 0px 29px #e7cb8f}83%{opacity:.4;text-shadow:0px 0px 29px #e7cb8f}87%{opacity:1;text-shadow:0px 0px 29px #e7cb8f}}
.home{text-align:center;background:url(/react-portfolio/static/media/lightbulb.3338ca49.jpg);background-position:center;background-repeat:no-repeat;background-size:100% 100%;background-color:#000;background-blend-mode:exclusion;background-size:cover;animation:home 10s}@keyframes home{from{opacity:0}to{opacity:1}}.name{padding-top:5vh;font-family:"Gill Sans","Gill Sans MT",Calibri,"Trebuchet MS",sans-serif}
.header-nav{background-color:#000;display:flex;z-index:999 !important;justify-content:right;width:100%}.fixed-nav{position:fixed;top:0;width:100%}.header-link{font-size:2.5vh;margin:10px;color:#e7cb8f}.header-link:hover{cursor:pointer}
.about{display:flex;flex-direction:column;justify-content:center;background-color:#fff}.intro p{font-family:"Franklin Gothic Medium","Arial Narrow",Arial,sans-serif;font-size:20px}
.project-modal-container{z-index:999;position:fixed;top:15%;left:30%;width:40%;height:70%;background-color:#fff;text-align:left}.close{position:absolute;right:15px;bottom:10px;font-size:40px;cursor:pointer}.project-modal-title{text-align:left;color:#000;position:relative;font-size:30px;font-family:"Kalam",cursive;margin:0px}.project-modal-image{width:100%;height:65%;background-color:#000;background-size:cover;border-bottom:4px solid #000}.project-modal-bottom{padding:15px}.project-modal-btn-container{position:absolute;bottom:20px}.project-header{display:flex;justify-content:flex-start;align-items:center;border-bottom:2px solid #e7cb8f}.project-languages{font-family:"Kalam",cursive;font-size:20px;margin-left:20px}.project-modal-btn{background-color:#e7cb8f;border-radius:5px;padding:10px;margin-right:10px;cursor:pointer;text-decoration:none}.project-modal-btn:hover{background-color:#fff;color:#000;border:1px solid #e7cb8f}
.work{background-color:#f5f3f3;min-height:100vh}.company-navbar{display:flex;justify-content:center;margin-top:5vh}.company-link{margin:10px;cursor:pointer;font-weight:500}.company-selected{border-bottom:4px solid #e7cb8f;padding-bottom:5px}.company-container{margin-top:3vh;margin-bottom:200px;padding-left:15px;text-align:left;display:flex;flex-direction:column;align-content:flex-start;border-radius:10px;animation:fadein 3s ease}@keyframes fadein{from{opacity:0}to{opacity:1}}.duties-list{padding:1vw;margin-left:-18px}.date{margin:0;font-weight:500}.title{background-color:rgba(231,203,143,.7);font-size:20px;font-weight:600;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:5px;border-radius:10px}.projects-container{display:flex;flex-wrap:wrap;justify-content:center;align-items:center;margin-top:90px;margin-bottom:200px}.project{min-width:200px;min-height:200px;background-color:#000;background-size:200px 200px;margin:10px;opacity:.6}.project-title{opacity:0;font-family:"Kalam",cursive}.project:hover{position:relative;bottom:25px;opacity:1;cursor:pointer;background-image:none}.project:hover+.project-title{opacity:1;transition:opacity 2s;margin-top:-20px}
.skills{background-color:#fff}.flex-box{display:flex;justify-content:space-between;align-items:center}.bar-container{display:flex;flex-direction:column;min-width:60%;min-height:30vh}.bar{display:flex;background-color:#f5f3f3;height:40px;width:100%;margin:10px;margin-left:0;border-radius:15px}.bar-label{display:inline-flex;min-width:100px;background-color:#f5f3f3;margin-left:10px;align-items:center}.bar-fill{background-color:#e7cb8f;transition:2.5s ease;transition-delay:.7s}.education-container{width:100%;min-height:50vh}.course-container{min-width:70%;display:flex;flex-direction:column;align-items:flex-start;margin:10px;margin-bottom:80px;padding:15px;border-top:2px solid #e7cb8f;border-left:4px solid #e7cb8f;background-color:#f5f3f3}.course-source{width:150px;margin-bottom:10px}.course-title{margin:0}.course-description{text-align:left}ul{padding:0}.blue{color:blue}.course-pic{display:inline;width:20%}.course-pic img{width:100%}.course-pic img:hover{-webkit-transform:scale(6);-moz-transform:scale(6);-ms-transform:scale(6);-o-transform:scale(6);position:relative;right:350px}.title-info{text-align:left}
.contact{width:100%;background-color:#f5f3f3}.contact-info h3{color:#e7cb8f;text-shadow:.05px .05px #000}.form{display:flex;flex-direction:column;justify-content:space-between;min-height:75%;min-width:50%}input{background-color:#fff;border:2px solid #e7cb8f;padding:10px;margin-bottom:20px;font-family:"Kalam",cursive}.contact-message{min-height:150px;font-family:"Kalam",cursive;background-color:#fff;border:2px solid #e7cb8f;padding:10px}.submit-btn{width:75%;margin:20px auto;border-radius:10px;color:#000;background-color:#e7cb8f;cursor:pointer}.submit-btn:hover{background-color:#fff;border:2px solid #e7cb8f}
